@use '../abstracts' as *;
/**
 * Page
 * - Description of page styles
 */
.wp-theme-style {
    color: #fff;
    padding: 2px;
    appearance: auto; // Added to test autoprefixer via postcss
    direction: ltr; // Added to test rtlcss

    // Added to test 'greater than breakpoint' mixin.
    @include breakpoint-up(lg) {
        background-color: #21759b;
    }

    // Added to test 'less than breakpoint' mixin.
    @include breakpoint-down(md) {
        background-color: #d54e21;
    }

    // Added to test 'between breakpoints' mixin.
    @include breakpoint-between(md, lg) {
        background-color: #7ad03a;
    }
}
